import React, { Component } from 'react'

export default class SignInForm extends Component {
  render(){
    return (
      <form className="signIn" onSubmit={this.props.onSubmit}> {/**/}
        <div className="row">
          <label></label>
          <input type="text" value={this.props.formData.username} placeholder="your name or email"
            onChange={this.props.onChange.bind(null,'username')} />
        </div>
        <div className="row">
          <label></label>
          <input type="password" value={this.props.formData.password} placeholder="password"
            onChange={this.props.onChange.bind(null,'password')} />
        </div>
        <div className="row action">
          <button type="submit"></button>
          <p onClick={this.props.onForgotPassword}></p>
        </div>
      </form>      
    )
  }
}


